<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
		<v-touch @tap="log">11111</v-touch>
  </div>
</template>

<script>
import { getHelp } from '../api/orderList';
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    };
	},
	mounted () {
		console.log(this.$store.getters.getAuthor);
		getHelp({'pageNum': 1, 'pageSize': 20}).then((res) => {
			console.log(res);
		});
	},
	methods: {
		log () {
			console.log(11);
		}
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

</style>



