import { STAGE_BASE_URL } from './config';
import axios from 'axios';

export function getHelp (obj) {
  const url = STAGE_BASE_URL + '/h5gateway/article/getArticleList';
  return axios.post(url, obj).then((res) => {
    return Promise.resolve(res.data);
  });
}



